import {Component, Element, Event, EventEmitter, h, Host, Prop, Watch} from '@stencil/core';

@Component({
  tag: 'dc-ui-tab-navigation-item',
  styleUrl: 'dc-ui-tab-navigation-item.scss',
  shadow: true,
})
export class DcUiTabNavigationItem {

  @Element() host: HTMLElement;

  @Prop() label: string;
  @Prop() active: boolean = false;
  @Prop() disabled: boolean = false;

  @Event() changeTabItem: EventEmitter<void>;

  @Watch('label')
  @Watch('disabled')
  changeTab() {
    this.changeTabItem.emit();
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    );
  }

}
